/* Updated CSS with increased container size and relative/dynamic properties */

body {
  font-family: 'Roboto', sans-serif;
  background-color: #000;
  color: #b0b0b0;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container {
  background-color: #0c0c0c;
  border: 1px solid #1f1f1f;
  border-radius: 1px;
  padding: 3%; /* Dynamic padding */
  box-shadow: 0px 10px 10px rgba(83, 83, 83, 0.2);
  text-align: center;
  width: 100%;
  max-width: 100%; /* 80% of the viewport width */
  animation: none;
  
}

h2 {
  margin: 2%;
  padding: 2%;
  color: #3498db;
  font-size: 2vw; /* Relative font size */
}

input {
  width: 80%;
  padding: 2% 3%; /* Dynamic padding */
  margin: 2% 0; /* Dynamic margin */
  border: 1px solid #333;
  border-radius: 5px;
  background-color: #222;
  color: #b0b0b0;
  font-size: 1.4vw; /* Relative font size */
}

button {
  background-color: #16a085;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 1.5% 3%; /* Dynamic padding */
  font-size: 1vw; /* Relative font size */
  cursor: pointer;
}

button:hover {
  background-color: #138e72;
}

a {
  text-decoration: none;
  color: #3498db;
  font-size: 1vw; /* Relative font size */
}

a:hover {
  text-decoration: underline;
  font-size: 1vw; /* Relative font size */
}
table {
  width: 100%;
  border-collapse: collapse;
  font-size: 1vw; /* Relative font size for table */
  margin: 2% 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  display: flexbox;
  width: auto;
  table-layout: fixed;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: stretch; /* Dynamic margin for table */
}

table th {
  background-color: #0c0c0c;
  width: 25%;
  color: #3498db;
  padding: 1% 2% 1% 2%; /* Dynamic padding for table headers */
  text-align: left;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  font-size: 1.2vw; /* Relative font size for table headers */
}

table td {
  padding: 1% 2% 1% 2%; /* Dynamic padding for table cells */
  font-size: 0.75vw;
  width: 25%;
  text-align: left;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  flex-direction: column;
   /* Relative font size for table cells */
}

table td button{
  width: 80%;
  margin: 2%;
  font-size: 0.75vw;
}

#delete:hover
{
  background-color: #a62c2b;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  background-color: #0c0c0c;
  padding: 1% 95%;
  float: right;
  position: fixed;
  top: 0;
  left: 0;
  width: 20%;
  z-index: 1000;
}


.user-name {
  color: #3498db;
  font-size: 1.4vw;
  margin-right: 1%;
}



.logout-button {
  float: right;
  background-color: #16a085;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 1% 2%;
  font-size: 1.2vw;
  cursor: pointer;
}

.logout-button:hover {
  background-color: #a62c2b;
}


